import React from 'react';
import Checkboxes from './Checkboxes';

function Generator (props) {

	return(
		<div className="generator">

	        <div className="generator-url-text">
	        <p>Link</p>
	        <input type="text" onChange={(event) => { props.setUrl(event.target.value) }} value={props.url} placeholder={'Your awesome link'} />
	        <p>Text</p>
	        <input type="text" onChange={(event) => { props.setText(event.target.value) }} value={props.text} placeholder={'Your awesome text'} />
	        </div>

	        <div className="generator-networks">
	          <p>Social networks</p>
	          <Checkboxes
	            medialist={props.medialist}
	            onChange={props.handleSelect}
	            selected={props.selected} />
	        </div>
	        
	        <div className="radio">
		        <div className="generator-radio size">
		          <p>Size</p>
		          <div className="generator-radio-inner-wrapper">
			          <input type="radio" id="radio-small" name="size" value="small" onChange={props.handleSize} checked={props.size === "small"} />
			          <label htmlFor="radio-small">Small</label>
			        
			          <input type="radio" id="radio-medium" name="size" value="medium" onChange={props.handleSize} checked={props.size === "medium"} />
			          <label htmlFor="radio-medium">Medium</label>
			          
			          <input type="radio" id="radio-large" name="size" value="large" onChange={props.handleSize} checked={props.size === "large"} />
			          <label htmlFor="radio-large">Large</label>
							</div>
		        </div>

		        <div className="generator-radio corners">
		          <p>Corner radius</p>
	          	<div className="generator-radio-inner-wrapper">
			          <input type="radio" id="radio-circle" name="corners" value="circle" onChange={props.handleCorners} checked={props.corners === "circle"} />
			          <label htmlFor="radio-circle">Circle</label>
			        
			          <input type="radio" id="radio-rounded" name="corners" value="rounded" onChange={props.handleCorners} checked={props.corners === "rounded"} />
			          <label htmlFor="radio-rounded">Rounded</label>
			          
			          <input type="radio" id="radio-square" name="corners" value="square" onChange={props.handleCorners} checked={props.corners === "square"} />
			          <label htmlFor="radio-square">Square</label>
							</div>
		        </div>
		    </div>

	    </div>
	)
}

export default Generator;