import React from 'react'
import encodeURI from '../utils/encodeURI'

function SharingButton (props) {
	const url = encodeURI(props.url);
	const space = "\n";
	let buttonLabel;

	if (props.size === "small") {
		buttonLabel = "";
	} else if (props.size === "medium") {
		buttonLabel = <span>{props.option.label}</span>
	} else {
		buttonLabel = <span>{props.option.shareBy ? "Share by " : "Share on "}{props.option.label}</span>
	}

	return (
		<>
			{space}
			<a className={"social-sharing-button "+props.corners} id={props.option.name} href={props.option.url.replace(/<url>/g, url).replace(/<text>/g, props.text)} target="_blank" rel="noopener noreferrer">
				{space}
				{props.option.icon}
				{buttonLabel}
				{space}
			</a>
			{space}
		</>
	)
}

export default SharingButton;