import React from 'react'

function CssGenerator (props) {

	let star = "";
	let socialSharingButton = "";
	let span = "";
	let cornerRadius = "";

	if (Object.values(props.selected).includes(true)) {
		star = "* {transition-property: background-color, transform;transition-duration: 0.3s;transition-timing-function: ease;}";
		socialSharingButton = ".social-sharing-button {display: inline-flex;align-items: center;height: 2em;padding: 0.25em 0.5em;margin: 0.5em;text-decoration: none;}.social-sharing-button:hover {transform: translateY(-4px);}.social-sharing-button svg {width: 1.5em;height: 1.5em;fill: #FFF;}";
	
		if (props.size === "small") {
			span = "";
		} else if (props.size === "medium" || "large") {
			span = ".social-sharing-button span {font-size: 0.9em;margin-left: 0.5em;color: #FFF;}";
		}

		if (props.corners === "circle") {
			cornerRadius = ".circle {border-radius: 100%;}";
		} else if (props.corners === "rounded") {
			cornerRadius = ".rounded {border-radius: 10px;}";
		} else if (props.corners === "square") {
			cornerRadius = ".square {border-radius: 0;}"
		}

	}

	const colors = [
		{
			name: 'facebook',
			color: '#3b5998',
			hover: '#2d4373'
		},
		{
			name: 'twitter',
			color: '#55acee',
			hover: '#2795e9'
		},
		{
			name: 'tumblr',
			color: '#35465C',
			hover: '#222d3c'
		},
		{
			name: 'pinterest',
			color: '#bd081c',
			hover: '#8c0615'
		},
		{
			name: 'e-mail',
			color: '#777',
			hover: '#5e5e5e'
		},
		{
			name: 'linkedin',
			color: '#0077b5',
			hover: '#046293'
		},
		{
			name: 'reddit',
			color: '#5f99cf',
			hover: '#3a80c1'
		},
		{
			name: 'xing',
			color: '#1a7576',
			hover: '#114c4c'
		},
		{
			name: 'whatsapp',
			color: '#25D366',
			hover: '#1da851'
		},
		{
			name: 'hackernews',
			color: '#FF6600',
			hover: '#d05608'
		},
		{
			name: 'vk',
			color: '#507299',
			hover: '#43648c'
		},
		{
			name: 'telegram',
			color: '#54A9EB',
			hover: '#4B97D1'
		},
		{
			name: 'messenger',
			color: '#0078FF',
			hover: '#0252ad'
		},
		{
			name: 'skype',
			color: '#00AFF0',
			hover: '#008abd'
		},
		{
			name: 'pocket',
			color: '#EF4056',
			hover: '#c43446'
		},
		{
			name: 'wechat',
			color: '#2dc100',
			hover: '#229000'
		}
	];

	const prettify = (code) => {
		return code.replace(/{/g, "{\n").replace(/;/g, ";\n").replace(/}/g, "}\n\n");
	}

	return (
		<div className="code css">
	        <p>CSS Code</p>
	        <code>
						{prettify(star)}
						{prettify(socialSharingButton)}
						{prettify(span)}
						{prettify(cornerRadius)}
						{
							colors.filter(option => {
								return(props.selected[option.name]);
							})
							.map(option => {
								return(
									<div key={"CSS "+option.name}>
										{"#"+option.name+" {\n"}
										{"background: "+option.color+";\n}\n\n"}
										{"#"+option.name+":hover {\n"}
										{"background: "+option.hover+";\n}\n\n"}
									</div>
								)								
							})
						}
	        </code>
     	</div>
	)
}

export default CssGenerator;