import React from 'react'
import ReactDOMServer from 'react-dom/server';
import SharingButton from './SharingButton';

function HtmlGenerator (props) {
const htmlString = ReactDOMServer.renderToStaticMarkup(
  props.medialist.filter(option => {
    return(props.selected[option.name]);
  })
  .map(option => {
    return (
      <SharingButton key={option.name}
        option={option}
        text={props.text}
        url={props.url}
        size={props.size}
        corners={props.corners}
        comments="true"
      />
    )
  })
)

const htmlStringPretty = htmlString.replace(/&lt;/g, "<").replace(/&gt;/g, ">");

	return (
		<div className="code html">
      <p>HTML Code</p>
      <code>
        {htmlStringPretty}
      </code>
    </div>
	)
}

export default HtmlGenerator;