import React from 'react';

function Checkboxes (props) {
	return (
	<div className="social-checkboxes">

		{props.medialist.map(checkbox => (
			<label className="container" key={"Container"+checkbox.name}>
				<input type="checkbox" key={"Input"+checkbox.name} name={checkbox.name} onChange={props.onChange} checked={props.selected[checkbox.name] || false}  alt={checkbox.label} />
				<span className="checkmark" key={"Checkmark"+checkbox.name}>
					{checkbox.icon}
				</span>
			</label>
		))}
	</div>
	)
}

export default Checkboxes;